.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;   
}

.navbar-links
{
flex:1;
display: flex;
justify-content: flex-start;
align-items: center;
}
.navbar-links_logo{
margin-right: 2rem;
}
.navbar-links_logo img {
width:62.56px;
height:16.02px;
}

.navbar-links_container{
    margin:-25px 0 0px 90px;
    display: flex;
    flex-direction: row;
}

.navbar-sign {
     display: flex;
     justify-content: flex-end;
     align-items: center;
}

.navbar-links_container p,
.navbar-sign p,
.navbar-menu_container p{
    color:#FFFFFF;
    font-family: var(--font-family);
    font-weight: 500;
    font-size:16px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}


.navbar-sign button,
.navbar-menu_container{
    padding:0.5rem 1rem;
    color:#FFFFFF;
    background:#FF4820;
    font-family: var(--font-family);
    font-weight:500;
    font-size: 16px;
    line-height: 25px;
    border-radius: 5px;
    border:0;
    outline: none;
    cursor:pointer;
}

.navbar-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}

.navbar-menu svg{
    cursor: pointer;
}
.navbar-menu_container-links-sign button{
    padding:0.5rem 1rem;
    color:#FFFFFF;
    background:#FF4820;
    font-family: var(--font-family);
    font-weight:500;
    font-size: 16px;
    line-height: 25px;
    border-radius: 5px;
    border:0;
    outline: none;
    cursor:pointer;
}
.navbar-menu_container
{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top:40px;
    right:0;
    margin: top 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}

.navbar-menu_container p{
    margin :1rem 0;

}
.navbar-menu_container-links-sign{
 display: none;   
}

@media screen and (max-width:1050px){
    .navbar-links_container{
        display: none;
    }
    .navbar-menu{
        display: flex;
    }
}


@media screen and (max-width:700px){
    .navbar{
       padding:2rem 4rem;
    }
}

@media screen and (max-width:550px){
    .navbar{
       padding:2rem;
    }
    .navbar-sign{
        display: none;
    }
    .navbar-menu_container{
        top:20px;
    }
    .navbar-menu_container-links-sign{
        display: block;
    }
}